export const mainnet = {
  name: 'mainnet',
  chainID: 'columbus-5',
  lcd: 'https://lcd.terra.dev',
};

export const testnet = {
  name: 'testnet',
  chainID: 'bombay-12',
  lcd: 'https://bombay-lcd.terra.dev',
};

export const localterra = {
  name: 'localterra',
  chainID: 'localterra',
  lcd: 'http://localhost:1317',
};

export const ChainProvider = { 0: testnet, 1: mainnet };

export const WalletConnectOptions = {
  bridge: 'https://walletconnect.terra.dev/',
};

export const loader = (chainID?: string | null) => {
  let isProduction = !chainID || chainID.startsWith('columbus-');

  if (process.env.NODE_ENV === 'development') {
    isProduction = false;
  }

  return {
    isProduction,
    chainProvider: ChainProvider,
    walletConnectOptions: WalletConnectOptions,
  };
};
